export default{
	data(){
		return{
			id:0,
			detail:{}
		}
	},
	onLoad(options) {
		console.log(options)
		this.id = options.id
		this.getDetail()
	},
	methods:{
		async getDetail(){
			const res = await this.$myRequest({
				url:'/api/getnew/'+this.id
			})
			this.detail = res.data.message[0]
			
		}
	}
}
